body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.city {
  --scale: 1;
  max-width: 90px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.city__flag {
  width: 25px;
  height: 35px;
  cursor: pointer;
}

.city__title {
  margin-bottom: 5px;
  font-size: 18px;
  line-height: 1;
  font-weight: 700;
  color: #222;
  text-shadow: 
    0 0 4px #fff,
    0 0 4px #fff,
    0 0 4px #fff;
}

.channels {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.channel {
  margin: 2.5px;
  width: 25px;
  height: 25px;
  background-color: #f2f2f2;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  border-radius: 5px;
  cursor: pointer;
}

.ch_2,
.ch_6,
.ch_7 {
  -webkit-order: 1;
          order: 1;
  width: 80px;
  height: 20px;
}

.ch_3,
.ch_8 {
  -webkit-order: 2;
          order: 2;
  width: 40px;
  height: 20px;
}


.on {
  box-shadow: 0 0 0 2px #6e0, 0 0 5px #6e0;
}

.off {
  box-shadow: 
  inset 0 0 0 20px rgba(130,0,0,.5),
  0 0 0 2px #f30;
}

